import React, { useState } from 'react';
import axios from 'axios';
import { Row, Col, Form, Button, FormGroup, FormControl, FormLabel } from "react-bootstrap";
import { Router, browserHistory, Route, Link } from 'react-router';

function Register(props) {
  const [loading, setLoading] = useState(false);
  const fname = useFormInput('');
  const lname = useFormInput('');
  const dob = useFormInput('');
  const [gender,setGender] = useState('');
  const [error, setError] = useState(null);
  const [gFemale, statusFemale] = useState(false);
  const [gMale, statusMale] = useState(false);
  // handle button click of login form
  const handleForm = (e) => {
    e.preventDefault();
    setError(null);
    if(fname.value == '') {
       setError("Enter your first name");
       return false;
    }
    if(lname.value == '') {
       setError("Enter your last name");
       return false;
    }
    if(gender == '') {
       setError("Select your gender");
       return false;
    }
    if(dob.value == '') {
       setError("Select your date of birth");
       return false;
    }
    setLoading(true);
    axios.post('https://techespo.com/pwa-react/api/signup.php',
      { fname: fname.value, lname: lname.value,gender : gender, dob:dob.value }).then(response => {
      setLoading(false);
      if(response.data.data != '') { 
        setError("Successfully signup");
        browserHistory.push('/home');
      }
    }).catch(error => {
      setLoading(false);
      setError("Something went wrong. Please try again later.");
    });
  }

  return (
    <div className="registerScreen">
      <Form>
        <h4>What's Your Name?</h4>
        <FormGroup className="nameText" controlId="fname">
          <FormControl autoFocus type="text" {...fname} placeholder="First Name"/>
        </FormGroup>
        <FormGroup controlId="lname">
          <FormControl {...lname} type="text" placeholder="Last Name"/>
        </FormGroup>
        <h4>And your gender?</h4>
        <Row className="nameText">
        <Col className="gender" md={6}>
        <FormGroup controlId="gender">
          <FormControl className={gMale} {...gender} type="text" value="Male" 
          onClick={() => { statusMale('select');statusFemale(null);setGender('Male');}}/>
        </FormGroup>
        </Col>
        <Col className="gender" md={6}>
        <FormGroup controlId="gender">
          <FormControl className={gFemale} {...gender} type="text" value="Female" 
          onClick={() => { statusFemale('select');statusMale(null);setGender('Female');}}/>
        </FormGroup>
         </Col>
        </Row>
        <h4>What's your date of birth?</h4>
        <FormLabel className="dobInstruction"><p>This Can't be changed later.</p></FormLabel>
        <FormGroup className="nameText" controlId="dob">
          <FormControl type="date" {...dob} placeholder="First Name"/>
        </FormGroup>
        <a href="#" className="next round" onClick={handleForm} disabled={loading} >&#8250;</a>
        {error && <><small style={{ color: 'red' }}>{error}</small><br /></>}<br />
        </Form>
    </div>
  );
}

const useFormInput = initialValue => {
  const [value, setValue] = useState(initialValue);

  const handleChange = e => {
    setValue(e.target.value);
  }
  return {
    value,
    onChange: handleChange
  }
}

export default Register;